.prpb {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.prpb__name {
  font-weight: 400;
  font-size: 24px;
  line-height: 1.5;
  color: #2B5CFB;
  border-bottom: 1px solid #A1A4B1;
  margin-top: 25px;
  margin-bottom: 32px;
}

.prpb__wrap {
  padding: 0 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.prpb__body {
  flex: 1;
}

.prpb__footer {
  display: flex;
  border-top: 1px solid #A1A4B1;
  margin-top: 32px;
  padding-top: 32px;
  padding-bottom: 140px;
  flex-wrap: wrap;
}
.prpb__footer .btn {
  margin-right: 40px;
  margin-bottom: 10px;
}
.prpb__footer .btn:last-child {
  margin-right: 0;
}

.prpb__hr {
  margin-top: 32px;
  margin-bottom: 32px;
  border-top: 1px solid #A1A4B1;
}

.prpb__label {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: #494653;
  margin-bottom: 4px;
}

.prpb__input {
  height: 48px;
  max-width: 384px;
  width: 100%;
  font-size: 20px;
  line-height: 32px;
}

.prpb__error__item {
  color: #EB5757;
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  display: flex;
  align-items: flex-start;
  margin-bottom: 6px;
}
.prpb__error__item:before {
  content: "!";
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  min-width: 20px;
  background-color: #EB5757;
  border-radius: 100%;
  color: #fff;
  margin-right: 7px;
}

.prpb__category__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 28px;
  color: rgba(0, 0, 0, 0.8);
  max-width: 547px;
  margin-bottom: 22px;
  margin-top: 20px;
}

.prpb__btn {
  color: #2D9CDB;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  border: 0;
  background-color: transparent;
  padding: 0;
  transition: 0.3s all;
}
.prpb__btn:hover {
  text-decoration: underline;
}

.prpb__category__wrap {
  display: none;
  flex-wrap: wrap;
  gap: 0 96px;
}
.prpb__category__wrap.active {
  display: flex;
}

.prpb__category__col {
  padding-left: 5px;
}

.prpb__category__item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 10px;
  cursor: pointer;
}
.prpb__category__item input {
  display: block;
  z-index: -99;
  opacity: 0;
  position: absolute;
}
.prpb__category__item input + div {
  display: flex;
  color: #505565;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}
.prpb__category__item input + div:before {
  content: "";
  display: block;
  width: 18px;
  min-width: 18px;
  height: 18px;
  margin-right: 11px;
  border-radius: 0.25em;
  border: 2px solid #A1A4B1;
}
.prpb__category__item input:checked + div:before {
  border: 2px solid #0d6efd;
  background-color: #0d6efd;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3e%3cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='M6 10l3 3l6-6'/%3e%3c/svg%3e");
}
.prpb__category__item input:focus + div:before {
  box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
}

#prpb__category__btn {
  margin-top: 10px;
}

#prpb__category__btn.active {
  display: none;
}

@media (max-width: 767px) {
  .prpb__input {
    font-size: 16px;
    line-height: 24px;
  }

  .prpb__error__item {
    font-size: 14px;
    line-height: 22px;
  }

  .prpb__category__text {
    font-size: 14px;
    line-height: 26px;
  }

  .prpb__footer {
    padding-bottom: 50px;
    justify-content: space-between;
  }

  .prpb__footer .btn {
    margin: 0;
  }

  .prpb__name {
    font-size: 20px;
    margin-top: 25px;
  }

  .prpb__wrap {
    padding: 0 5px;
  }
}
.prpb__breadcrumb {
  padding-top: 5px;
}
.prpb__breadcrumb ul {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 10px 0;
  margin: 0;
  padding: 0;
}
.prpb__breadcrumb li {
  list-style: none;
  position: relative;
  display: flex;
  align-items: center;
}
.prpb__breadcrumb li a, .prpb__breadcrumb li span {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #001828;
}
.prpb__breadcrumb li a:hover {
  text-decoration: underline;
}
.prpb__breadcrumb li:after {
  content: "";
  border: 5px solid transparent;
  border-top: 5px solid #0A243F;
  transform: rotate(-90deg);
  margin-left: 10px;
  margin-right: 2px;
  position: relative;
  bottom: -1px;
}
.prpb__breadcrumb li:last-child:after {
  display: none;
}

@media (min-width: 1350px) {
  .prpb__breadcrumb {
    margin-left: -32px;
  }
}
.exp-content--prbp {
  margin-top: 30px;
}

.prbp__ul {
  padding: 0;
  margin: 0;
  margin-bottom: 6px;
}
.prbp__ul li {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: flex-start;
  list-style: none;
  margin-bottom: 12px;
}
.prbp__ul li:before {
  content: "";
  width: 7px;
  min-width: 7px;
  height: 7px;
  margin-top: 7px;
  margin-right: 4px;
  background-color: #C4C4C4;
  border-radius: 7px;
}

.prpb__textarea textarea {
  min-height: 156px;
  width: 100%;
  border: 1px solid #D7D6DC;
  border-radius: 4px;
  padding: 6px 11px;
  resize: vertical;
}
.prpb__textarea textarea::placeholder {
  color: #79758A;
}

.prpb__textarea__counter {
  font-weight: 700;
  font-size: 15px;
  line-height: 24px;
  color: #C4C4C4;
  text-align: right;
}